import Vue from 'vue'
import Router from 'vue-router'

import Home from '../components/Home.vue'
import ListFile from '../components/ListFile.vue'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/list_file',
      name: 'ListFile',
      component: ListFile
    }
  ]
})



